@font-face {
	font-family  : "Inter";
	src          : url('../fonts/Inter-Regular.woff2') format("woff2"),
	url("../fonts/Inter-Regular.woff") format("woff"),
	url("../fonts/Inter-Regular.ttf") format("truetype");
	font-weight  : 400;
	font-style   : normal;
	font-display : swap;
}

@font-face {
	font-family  : "DrukCyr";
	src          : url('../fonts/DrukWideCyr-Medium.woff2') format("woff2"),
	url("../fonts/DrukWideCyr-Medium.woff") format("woff"),
	url("../fonts/DrukWideCyr-Medium.ttf") format("truetype");
	font-weight  : 500;
	font-style   : normal;
	font-display : swap;
}
