.tabs__content {
	display : none;

	&.active {
		display : block;
	}
}

.tabs--vacancies {
	margin-bottom : 3rem;
	grid-column   : 4 span;
	grid-row      : 2/2;

	@media screen and (min-width : $break-table) {
		margin-bottom : 5.8rem;
		grid-column   : 12 span;
	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 7.3rem;
		grid-column   : 4/10;
	}
}

.tabs__head--vacancies {

}

.tabs__wrap {
	display         : flex;
	align-items     : center;
	justify-content : space-between;
	margin-bottom   : 3rem;
	gap             : 3.5rem;
	overflow-x      : auto;
	padding         : 0 0 0.8rem 0;

	@media screen and (min-width : $break-table) {
		margin-bottom : 7.5rem;
		padding       : 0 0 1.5rem 0;
	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 9.2rem;
	}
}

.tabs__button--vacancies {
	color            : var(--white);
	font-family      : var(--font-second);
	font-size        : 1.2rem;
	font-weight      : 500;
	line-height      : 1.4rem;
	letter-spacing   : 0.024rem;
	text-transform   : uppercase;
	opacity          : 0.8;
	background-color : transparent;
	border           : none;
	padding          : 0;
	cursor           : pointer;
	position         : relative;

	@media screen and (min-width : $break-table) {
		font-size      : 1.8rem;
		line-height    : 2.1rem;
		letter-spacing : 0.036rem;
	}

	@media screen and (min-width : $break-desktop) {
		font-size      : 2.2rem;
		line-height    : 5.4rem;
		letter-spacing : 0.044rem;
	}

	&.active {
		opacity : 1;

		&:before {
			content          : "";
			position         : absolute;
			bottom           : -8px;
			left             : 0;
			width            : 100%;
			height           : 0.3rem;
			background-color : var(--white);

			@media screen and (min-width : $break-table) {
				bottom : -1.5rem
			}

			@media screen and (min-width : $break-desktop) {

			}
		}
	}
}
