.page-wrapper {
	overflow : hidden;

}

.wrapper {
	box-sizing : border-box;
	max-width  : 100%;
	margin     : auto;
	padding    : 0 1.6rem;

	@media screen and (min-width : $break-table) {
		padding : 0 3.3rem;
	}

	@media screen and (min-width : $break-desktop) {
		padding : 0 6rem;
	}
}

.hero {
	padding-top   : 7.3rem;
	position      : relative;
	margin-bottom : 4.7rem;

	@media screen and (min-width : $break-table) {
		padding-top   : 7.7rem;
		margin-bottom : 15rem;
	}

	@media screen and (min-width : $break-desktop) {
		padding-top   : 29.5rem;
		margin-bottom : 14rem;
	}
}

.hero__wrapper {
	display        : flex;
	flex-direction : column;
	align-items    : flex-start;
}

.hero-title {
	color          : var(--stack-white, #FFF);
	font-family    : var(--font-second);
	font-size      : 3.6rem;
	font-weight    : 500;
	line-height    : 4.5rem;
	letter-spacing : 0.072rem;
	text-transform : uppercase;
	margin         : 0 0 2rem 0;
	transition     : transform .3s ease, opacity .3s ease;


	@media screen and (min-width : $break-table) {
		font-size      : 9rem;
		line-height    : 10.8rem;
		letter-spacing : 0.018rem;
		margin         : 0 0 4rem 0;
		animation      : none;
		opacity        : 1;
	}

	@media screen and (min-width : $break-desktop) {
		font-size      : 16.8rem;
		line-height    : 20rem;
		letter-spacing : 0.3367rem;
		margin         : 0 0 6rem 0;

	}

	span {
		display : block;
	}
}

.hero-title__top {
	color     : var(--white);
	animation : titleTop 1s ease forwards;
	transform : translateX(100%);
	opacity   : 0;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		padding-left : 58.7rem;

	}
}


.hero-title__bottom {
	color           : var(--primary);
	animation       : titleBottom 1.1s ease forwards;
	animation-delay : 0.5s;
	transform       : translateX(-100%);
	opacity         : 0;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {

	}
}


.hero-advantages {
	list-style       : none;
	padding          : 0;
	margin           : 0 0 2rem 0;
	align-items      : flex-start;
	display          : inline-flex;
	flex-direction   : column;
	transform        : translateY(-100%) rotate(-90deg);
	opacity          : 0;
	transform-origin : 50% 50%;
	gap              : 0.8rem;
	order            : -1;
	position         : relative;
	left             : 6.3rem;
	transition       : transform .3s ease, opacity .3s ease;
	animation        : toBottom 1.1s forwards ease;
	animation-delay  : 1s;

	@media screen and (min-width : $break-table) {
		gap    : 0.9rem;
		margin : 0 auto 2.7rem auto;
		left   : 0;
	}

	@media screen and (min-width : $break-desktop) {
		position : absolute;
		top      : 5.9rem;
		left     : 14.1rem;
		gap      : 1.8rem;
		margin   : 0;

	}
}

.hero-advantages__item {
	border-radius : 8.8rem;
	border        : 0.3rem solid var(--primary);
	padding       : 0 1.1rem;
	width         : max-content;

	@media screen and (min-width : $break-table) {
		padding : 0 1.6rem;
	}

	@media screen and (min-width : $break-desktop) {
		padding : 0 2.4rem;
	}

	&:first-child {
		transform : translate3d(0, -2.5rem, 0) rotate(-44deg);

		@media screen and (min-width : $break-table) {
			transform : translate3d(0, -3.5rem, 0) rotate(-44deg);
		}

		@media screen and (min-width : $break-desktop) {
			transform : translate3d(0, -6.2rem, 0) rotate(-44deg);
		}

		span {
			transform : rotate(180deg);
		}
	}

	span {
		color          : var(--stack-white, #FFF);
		font-family    : var(--font-second);
		font-size      : 1rem;
		font-weight    : 500;
		line-height    : 3.1rem;
		letter-spacing : 0.0214rem;
		text-transform : uppercase;
		display        : block;

		@media screen and (min-width : $break-table) {
			font-size      : 1.6rem;
			line-height    : 4.7rem;
			letter-spacing : 0.0321rem;
		}

		@media screen and (min-width : $break-desktop) {
			font-size      : 2.2rem;
			line-height    : 6.6rem;
			letter-spacing : 0.0456rem;
		}
	}
}

.hero-text {
	display               : grid;
	grid-template-columns : repeat(4, 1fr);
	margin-bottom         : 4.6rem;
	opacity               : 0;
	animation             : opacity .3s ease forwards;
	animation-delay       : 1.5s;
	transition            : opacity .3s ease;

	@media screen and (min-width : $break-table) {
		grid-template-columns : repeat(12, 1fr);
		margin-bottom         : 0;
	}

	@media screen and (min-width : $break-desktop) {

	}

	p {
		grid-column : 4 span;
		color       : var(--white);
		font-size   : 1.5rem;
		font-weight : 400;
		line-height : 2rem;
		margin      : 0;
		padding     : 0;

		@media screen and (min-width : $break-table) {
			grid-column : 7/13;
			font-size   : 2.2rem;
			line-height : 2.8rem;
		}

		@media screen and (min-width : $break-desktop) {
			grid-column : 4/7;
			font-size   : 2.3rem;
			line-height : 3.5rem;
		}
	}
}

.hero-vacancies {
	margin-left     : auto;
	animation       : opacity .3s ease forwards;
	animation-delay : 1.5s;
	transition      : opacity .3s ease;
	opacity         : 0;

	@media screen and (min-width : $break-table) {
		position : absolute;
		left     : 0;
		bottom   : -2.4rem;
		z-index  : 1;
	}

	@media screen and (min-width : $break-desktop) {
		bottom : 4rem;
		right  : 8.3rem;
		left   : unset;
	}
}


.hero-vacancies__wrap {
	width           : 13.6rem;
	height          : 13.6rem;
	position        : relative;
	display         : flex;
	align-items     : center;
	justify-content : center;

	@media screen and (min-width : $break-table) {
		width  : 18.1rem;
		height : 18.1rem;
	}

	@media screen and (min-width : $break-desktop) {
		width  : 36.1rem;
		height : 36.1rem;
	}
}

.hero-vacancies__logo {
	width     : 100%;
	height    : 100%;
	display   : inline-block;
	animation : rotate 15s linear infinite;

	img {
		width      : 100%;
		object-fit : cover;
	}
}

.hero-vacancies__count {
	color          : var(--white);
	font-family    : var(--font-second);
	font-size      : 5.2rem;
	font-weight    : 500;
	line-height    : 7.3rem;
	letter-spacing : 0.0144rem;
	text-transform : uppercase;
	position       : absolute;
	top            : 50%;
	left           : 50%;
	transform      : translate(-50%, -50%);

	@media screen and (min-width : $break-table) {
		font-size      : 7rem;
		line-height    : 9.9rem;
		letter-spacing : 0.1411rem;
	}

	@media screen and (min-width : $break-desktop) {
		font-size      : 13.1rem;
		font-weight    : 500;
		line-height    : 18.3rem;
		letter-spacing : 0.2623px;
	}
}

.vacancies {
	margin-bottom : 7rem;

	@media screen and (min-width : $break-table) {
		margin-bottom : 15rem;
	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 12rem;
	}
}

.vacancies__top {
	display               : grid;
	grid-template-columns : subgrid;
	grid-column           : 4 span;
	margin-bottom         : 5rem;

	@media screen and (min-width : $break-table) {
		grid-column   : 12 span;
		margin-bottom : 9rem;
	}

	@media screen and (min-width : $break-desktop) {
		grid-column   : 12 span;
		margin-bottom : 10rem;
	}
}

.vacancies-experience {
	grid-column   : 4 span;
	margin-bottom : 3rem;

	@media screen and (min-width : $break-table) {
		grid-column   : 1/2;
		margin-bottom : 0;
	}

	@media screen and (min-width : $break-desktop) {

	}

	&__top {
		color       : var(--white);
		font-size   : 5.2rem;
		font-weight : 400;
		line-height : normal;

		@media screen and (min-width : $break-table) {
			font-size : 11rem;
		}

		@media screen and (min-width : $break-desktop) {
			font-size : 12.3rem;
		}
	}

	&__bottom {
		color       : var(--white);
		font-size   : 1.6rem;
		font-weight : 400;
		line-height : 1.9rem;

		@media screen and (min-width : $break-table) {
			font-size   : 2rem;
			line-height : 2.5rem;
		}

		@media screen and (min-width : $break-desktop) {
			font-size   : 2.3rem;
			line-height : 3.2rem;
		}
	}
}

.vacancies__text {
	grid-column : 4 span;
	color       : var(--white);
	font-size   : 1.6rem;
	font-weight : 400;
	line-height : 2.6rem;

	@media screen and (min-width : $break-table) {
		grid-column : 6/13;
		font-size   : 2.2rem;
		line-height : 2.8rem;
	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 2.4rem;
		line-height : 3.9rem;
		grid-column : 4/9;
	}
}


.vacancies__list {
	display        : flex;
	list-style     : none;
	padding        : 0;
	margin         : 0;
	flex-direction : column;
	flex-wrap      : wrap;

	@media screen and (min-width : $break-table) {
		flex-direction  : row;
		justify-content : space-between;
		gap             : 2rem;
	}

	@media screen and (min-width : $break-desktop) {

	}

	li {
		width       : 100%;
		color       : var(--white);
		font-size   : 1.4rem;
		font-weight : 400;
		line-height : 2rem;
		padding     : 2rem 0;

		&:not(:last-child) {
			border-bottom : 0.1rem solid var(--gray);
		}

		@media screen and (min-width : $break-table) {
			width          : calc(50% - 4rem);
			font-size      : 1.7rem;
			line-height    : 2.5rem;
			padding-top    : 2.4rem;
			padding-bottom : 0;
			border-top     : 0.1rem solid var(--gray);

			&:not(:last-child) {
				border-bottom : none;
			}
		}

		@media screen and (min-width : $break-desktop) {
			font-size   : 2rem;
			line-height : 3.2rem;
			width       : calc(50% - 5rem);
		}

		&:not(:last-child) {

			@media screen and (min-width : $break-table) {
				margin-bottom : 4rem;
			}

			@media screen and (min-width : $break-desktop) {

			}
		}
	}
}

.connection {
	margin-bottom : 10rem;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 12rem;
	}
}

.connection__wrapper {


	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		display     : flex;
		align-items : center;
	}
}

.connection__img {
	width         : 28.8rem;
	height        : 24rem;
	margin-bottom : 3rem;
	transform     : translateX(-110%);

	@media screen and (min-width : $break-table) {
		width         : 70rem;
		height        : 57rem;
		margin-bottom : 6rem;
	}

	@media screen and (min-width : $break-desktop) {
		width         : 58rem;
		height        : 46rem;
		margin-bottom : 0;
	}

	img {
		width      : 100%;
		height     : 100%;
		object-fit : cover;
	}
}

.connection__text {
	color          : var(--white);
	font-family    : var(--font-second);
	font-size      : 1.8rem;
	font-weight    : 500;
	line-height    : 2.5rem;
	letter-spacing : 0.036rem;
	text-transform : uppercase;
	transition     : opacity .3s ease;
	opacity        : 0;

	@media screen and (min-width : $break-table) {
		font-size      : 2.5rem;
		line-height    : 3.5rem;
		letter-spacing : 0.05px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-left    : 15rem;
		font-size      : 3.5rem;
		line-height    : 4.9rem;
		letter-spacing : 0.07rem;
		max-width      : 80.8rem;
	}

	a {
		position : relative;
		display  : inline-block;

		&:before {
			background-color : var(--white);
			bottom           : 0;
			content          : "";
			display          : block;
			height           : 0.02rem;
			left             : 0;
			position         : absolute;
			right            : 0;
			transition       : background-color .3s ease;

			@media screen and (min-width : $break-table) {

			}

			@media screen and (min-width : $break-desktop) {
				height : 0.3rem;
				bottom : -0.2rem;
			}
		}

		&:hover {
			&:before {
				background-color : var(--primary);
			}
		}
	}
}

.btn-close {
	padding          : 0;
	background-color : transparent;
	border           : none;
	position         : absolute;
	top              : 1.6rem;
	right            : 1.6rem;
	width            : 2.2rem;
	height           : 2.2rem;
	cursor           : pointer;

	@media screen and (min-width : $break-table) {
		top    : 3.2rem;
		right  : 3.2rem;
		width  : 3.4rem;
		height : 3.4rem;
	}

	@media screen and (min-width : $break-desktop) {
		top    : 5.9rem;
		right  : 4.9rem;
		width  : 6.75rem;
		height : 6.75rem;
	}

	svg {
		width      : 100%;
		height     : 100%;
		object-fit : cover;
		max-height : none;
	}
}

@keyframes titleTop {
	100% {
		transform : translateX(0);
		opacity   : 1;
	}
}

@keyframes titleBottom {

	100% {
		transform : translateX(0);
		opacity   : 1;
	}
}

@keyframes toBottom {
	100% {
		transform : translateY(0) rotate(-90deg);
		opacity   : 1;
	}
}

@keyframes opacity {
	100% {
		opacity : 1;
	}
}


@keyframes rotate {
	0% {
		transform : rotate(0);
	}
	100% {
		transform : rotate(360deg);
	}
}

.animate-connection {
	animation  : arrowAnim 1s ease forwards;
	transition : opacity .3s ease, transform .3s ease;
}

@keyframes arrowAnim {

	100% {
		transform : translateX(0);
		opacity   : 1;
	}
}
