// -----------------------------------------------
//  Button config
// -----------------------------------------------

$btn-font-weight           : 400;
$btn-line-height           : 1.25;
$btn-border-width          : 1px;
$btn-padding               : 4px 24px;
$btn-font-size             : 28px;
$btn-border-radius         : 129px;
$btn-transition            : background .3s ease, color .3s ease, border-color .3s ease;
$btn-height                : 49px;

// OUTLINE

// SIZE
// Big
$btn-padding-big           : 4px 30px;
$btn-font-size-big         : 18px;
$btn-height-big            : 59px;

// Small
$btn-padding-small         : 4px 16px;
$btn-font-size-small       : 15px;
$btn-height-small          : 39px;

// COLOR
// White
$btn-white-bg              : #DCDCDE;
$btn-white-color           : #0B081B;
$btn-white-bg-hover        : #BDBDBF;
$btn-white-color-hover     : #0B081B;

// LOADING
$btn-loading-color         : #FFF;
$btn-loading-color-outline : #000;
// SPACING
$btn-spacing-x             : 5px;
$btn-spacing-y             : 5px;

// -----------------------------------------------
//  Button style
// -----------------------------------------------

.b-btn {
	font-size       : $btn-font-size;
	font-weight     : $btn-font-weight;
	line-height     : $btn-line-height;
	position        : relative;
	display         : inline-flex;
	align-items     : center;
	flex-flow       : row nowrap;
	justify-content : center;
	box-sizing      : border-box;
	min-height      : $btn-height;
	padding         : $btn-padding;
	user-select     : none;
	transition      : $btn-transition;
	text-align      : center;
	vertical-align  : middle;
	white-space     : nowrap;
	text-decoration : none;
	border          : $btn-border-width solid transparent;
	border-radius   : $btn-border-radius;


	&:hover,
	&:focus-within,
	&.focus {
		outline : 0;
	}

	&.disabled,
	&:disabled {
		cursor     : default;
		opacity    : .45;
		background : inherit;
		box-shadow : none;
	}

	&:active,
	&.active {

	}

	> svg {
		display : block;
		flex    : 0 0 auto;

		&:first-child {
			margin-right : 6px;
		}

		&:last-child {
			margin-left : 6px;
		}
	}

	// SIZE
	// Big
	&--big {
		font-size  : $btn-font-size-big;
		min-height : $btn-height-big;
		padding    : $btn-padding-big;
	}

	&--small {
		font-size  : $btn-font-size-small;
		min-height : $btn-height-small;
		padding    : $btn-padding-small;
	}

	// COLOR
	// Red
	&--primary {
		color            : var(--white);
		font-family      : var(--font-second);
		font-size        : 1.3rem;
		font-style       : normal;
		font-weight      : 500;
		line-height      : 1.2rem;
		letter-spacing   : 0.0265rem;
		text-transform   : uppercase;
		background-color : var(--primary);
		border-radius    : 6.1rem;
		padding          : 0.8rem 0;
		position         : relative;

		@media screen and (min-width : $break-table) {
			font-size      : 2.2rem;
			line-height    : 2.6rem;
			letter-spacing : 0.044rem;
			border-radius  : 12.9rem;
			padding        : 1.5rem 0;
		}

		@media screen and (min-width : $break-desktop) {
			font-size      : 2.8rem;
			letter-spacing : 0.056rem;
			padding        : 2.4rem 0;
		}

		&:hover,
		&:focus-within,
		&:active,
		&.active {
			background-color : var(--white);
			color            : var(--primary);

			&:before {
				background-color : var(--primary);
			}
		}

		&.disabled,
		&:disabled {
			&:hover,
			&:focus-within,
			&:active,
			&.active {

			}
		}

		&:before {
			background-color  : #FFF;
			background-repeat : no-repeat;
			background-size   : cover;
			content           : "";
			display           : inline-block;
			height            : 1.9rem;
			margin-right      : 1rem;
			mask-image        : url('../images/icons/mail.svg');
			mask-position     : 50% 50%;
			mask-repeat       : no-repeat;
			mask-size         : 100% 100%;
			transition        : background-color .3s ease;
			width             : 1.9rem;

			@media screen and (min-width : $break-table) {
				height       : 3rem;
				margin-right : 1.6rem;
				width        : 3rem;
			}

			@media screen and (min-width : $break-desktop) {
				height       : 36px;
				margin-right : 21px;
				width        : 36px;
			}
		}
	}

	// LOADING
	&--loading {
		&:before {
			content          : "";
			display          : inline-block;
			width            : 12px;
			height           : 12px;
			margin-right     : 10px;
			margin-left      : -15px;
			transition       : border .3s ease;
			animation        : loadingSpin 1s infinite linear;
			vertical-align   : middle;
			border           : 3px solid rgba($btn-loading-color, 0.6);
			border-top-color : $btn-loading-color;
			border-radius    : 50%;
		}

		&.outline {
			&:before {
				border           : 3px solid rgba($btn-loading-color-outline, 0.2);
				border-top-color : $btn-loading-color-outline;
			}

			&:hover {
				&:before {
					border           : 3px solid rgba($btn-loading-color, 0.6);
					border-top-color : $btn-loading-color;
				}
			}
		}
	}

	// FULL-SIZE
	&--full-size {
		display : block;
		width   : 100%;

		+ .full-size {
			margin-right : $btn-spacing-y;
		}
	}

	// SPACING
	& + .b-btn {
		margin-left : $btn-spacing-x;
	}
}

.b-btn-grouping {
	display : inline-flex;

	.b-btn {
		margin-right  : 0;
		border-radius : 0;

		&:first-child {
			border-radius : $btn-border-radius 0 0 $btn-border-radius;
		}

		&:last-child {
			border-radius : 0 $btn-border-radius $btn-border-radius 0;
		}

		&.outline {
			z-index     : 1;
			margin-left : -1px;

			&:hover {
				z-index : 2;
			}

			&.active {
				z-index : 3;
			}

			&:first-child {
				margin-left : 0;
			}
		}
	}
}

@keyframes loadingSpin {
	0% {
		transform : rotate(0deg);
	}
	100% {
		transform : rotate(360deg);
	}
}

// Future-proof disabling of clicks on `<a>` elements
a.b-btn.disabled,
fieldset[disabled] a.b-btn {
	pointer-events : none;
}

// Specificity overrides
input[type="submit"],
input[type="reset"],
input[type="button"] {
	&.full-size {
		width : 100%;
	}
}
