.accordion {
	border-bottom : 1px solid var(--border_color);

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {

	}

	&.open {
		.accordion__content {
			grid-template-rows : 1fr;
			padding-top        : 3rem;
			padding-bottom     : 4.4rem;

			@media screen and (min-width : $break-table) {
				padding-top    : 5.8rem;
				padding-bottom : 7.4rem;
			}

			@media screen and (min-width : $break-desktop) {
				padding-top    : 7.5rem;
				padding-bottom : 10rem;
			}
		}

		.accordion__icon {
			&:after {
				transform : translateX(-50%) rotate(90deg);;
			}
		;
		}
	}

	&:first-child {
		.accordion__control {
			border-top : 0.3rem solid var(--white);
		}
	}

	&:not(:first-child) {
		.accordion__control {
			&:before {
				content          : "";
				position         : absolute;
				top              : -0.3rem;
				left             : 0;
				width            : 100%;
				height           : 0.3rem;
				background-color : var(--white);
				z-index          : 1;
			}
		}
	}
}

.accordion__content {
	display            : grid;
	grid-template-rows : 0fr;
	transition         : grid-template-rows 500ms, padding-bottom 500ms, padding-top 500ms;

	@media screen and (min-width : $break-table) {
		padding : 0;
	}

	@media screen and (min-width : $break-desktop) {
		padding : 0;
	}
}

.accordion__main {
	overflow              : hidden;
	display               : grid;
	grid-template-columns : repeat(4, 1fr);
	grid-column           : 4 span;

	@media screen and (min-width : $break-table) {
		grid-template-columns : repeat(12, 1fr);
		grid-column           : 12 span;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.accordion__control {
	background-color : transparent;
	border           : none;
	cursor           : pointer;
	display          : flex;
	justify-content  : space-between;
	position         : relative;
	width            : 100%;
	align-items      : center;
	border-bottom    : 0.3rem solid var(--white);
	padding          : 1.7rem 0;
	gap              : 14px;

	@media screen and (min-width : $break-table) {
		padding : 4rem 0;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.accordion__title {
	color          : var(--white);
	font-family    : var(--font-second);
	font-size      : 2rem;
	font-style     : normal;
	font-weight    : 500;
	line-height    : 2.4rem;
	letter-spacing : 0.004rem;
	text-transform : uppercase;
	text-align     : start;

	@media screen and (min-width : $break-table) {
		font-size      : 4rem;
		line-height    : 4.8rem;
		letter-spacing : 0.08rem;
		max-width      : 60rem;
	}

	@media screen and (min-width : $break-desktop) {
		font-size      : 6.9rem;
		line-height    : 20rem;
		letter-spacing : 0.1387rem;
		max-width      : none;
	}
}

.accordion__icon {
	width       : 2.1rem;
	height      : 2.1rem;
	position    : relative;
	flex-shrink : 0;

	@media screen and (min-width : $break-table) {
		width  : 5rem;
		height : 5rem;
	}

	@media screen and (min-width : $break-desktop) {
		width  : 10.2rem;
		height : 9.6rem;
	}

	&:before {
		content          : "";
		position         : absolute;
		top              : 50%;
		transform        : translateY(-50%);
		left             : 0;
		width            : 100%;
		height           : 2px;
		background-color : var(--white);
		transition       : transform 500ms ease;

		@media screen and (min-width : $break-table) {
			height : 5px;
		}

		@media screen and (min-width : $break-desktop) {
			height : 1rem;
		}
	}

	&:after {
		content          : "";
		position         : absolute;
		top              : 0;
		left             : 50%;
		transform        : translateX(-50%) rotate(180deg);;
		width            : 2px;
		height           : 100%;
		background-color : var(--white);
		transition       : transform 500ms ease;

		@media screen and (min-width : $break-table) {
			width : 5px;
		}

		@media screen and (min-width : $break-desktop) {
			width : 1rem;
		}
	}

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {

	}

	img {
		height     : 100%;
		object-fit : cover;
		width      : 100%;
	}
}

.accordion--vacancies {
	.accordion__main {
		.b-btn--primary {
			grid-column : 4 span;

			@media screen and (min-width : $break-table) {
				grid-column : 12 span;
			}

			@media screen and (min-width : $break-desktop) {
				grid-column : 4/10;
			}
		}
	}
}
